<!--
 * @Description: 分销员审核
 * @Author: your name
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div>
    <!-- 筛选条件 -->
    <el-card class="filter-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="分销员昵称：">
            <el-input v-model="list.channelUsernickName"
                      clearable
                      placeholder="分销员昵称"></el-input>
          </el-form-item>
          <el-form-item label="分销员手机号：">
            <el-input v-model="list.channelUserPhone"
                      clearable
                      placeholder="分销员手机号"></el-input>
          </el-form-item>
          <el-form-item label="审核状态：">
            <el-select v-model="list.status"
                       clearable
                       placeholder="请选择">
              <el-option v-for="item in statusList"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="申请时间：">
            <el-date-picker v-model="list.registerTime"
                            type="datetimerange"
                            value-format="yyyy-MM-dd HH-mm-ss"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container"
             shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
    </el-card>
    <el-card class="operate-container"
             shadow="never">
      <el-table :data="tableList"
                style="width: 100%"
                ref="multipleTable"
                v-loading="loading"
                border>
        <el-table-column type="index"
                         width="50">
        </el-table-column>
        <el-table-column label="分销员昵称"
                         min-width="140"
                         align="center">
          <template slot-scope="scope">{{scope.row.channelUsernickName}}</template>
        </el-table-column>
        <el-table-column label="分销员手机号"
                         width="120"
                         prop="channelUserPhone"
                         align="center">
        </el-table-column>
        <el-table-column label="邀请方昵称"
                         width="160"
                         align="center">
          <template slot-scope="scope">
            {{scope.row.inviternickName == '' ? '--' : scope.row.inviternickName}}
          </template>
        </el-table-column>
        <el-table-column label="邀请方手机号"
                         width="130"
                         align="center">
          <template slot-scope="scope">
            {{scope.row.inviterPhone == '' ? '--' : scope.row.inviterPhone}}
          </template>
        </el-table-column>
        <el-table-column width="130"
                         align="center">
          <template slot="header"
                    slot-scope="scope">
            <el-tooltip class="item"
                        effect="dark"
                        content="累计交易笔数是指该用户支付过的订单笔数，剔除退款，不剔除部分退款"
                        placement="top">
              <el-button style="color:#909399;font-weight:600"
                         type="text">累计交易笔数<i class="el-icon-question"></i></el-button>
            </el-tooltip>
          </template>
          <template slot-scope="scope">
            {{scope.row.orderTotal == '' ? '--' : scope.row.orderTotal}}
          </template>
        </el-table-column>
        <el-table-column width="150"
                         align="center">
          <template slot="header"
                    slot-scope="scope">
            <el-tooltip class="item"
                        effect="dark"
                        content="累计自购金额是指该用户的实付金额，剔除退款"
                        placement="top">
              <el-button style="color:#909399;font-weight:600"
                         type="text">累计自购金额<i class="el-icon-question"></i></el-button>
            </el-tooltip>
          </template>
          <template slot-scope="scope">
            {{scope.row.orderAmount == '' ? '--' : scope.row.orderAmount}}
          </template>
        </el-table-column>
        <el-table-column label="申请加入时间"
                         width="160"
                         prop="createTime"
                         align="center">
        </el-table-column>
        <el-table-column label="状态"
                         width="120"
                         prop="applyState"
                         align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.applyState == 0">待审核</span>
            <span v-if="scope.row.applyState == 1">已通过</span>
            <span v-if="scope.row.applyState == 2">已驳回</span>
          </template>
        </el-table-column>
        <el-table-column label="操作"
                         min-width="250"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       v-if="scope.row.applyState == 0"
                       @click="auditTrue(1,scope.row)">审核通过</el-button>
            <el-button type="text"
                       v-if="scope.row.applyState == 0"
                       @click="auditTrue(2,scope.row)">驳回</el-button>
            <el-button type="text"
                       v-if="scope.row.applyState == 2"
                       @click="showReason(scope.row)">查看驳回原因</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 固定在下面 -->
      <div class="flex-row-end">
        <!-- 页码 -->
        <el-pagination background
                       style="margin-top: 10px;margin-left:20px"
                       @size-change="handleSizeChange"
                       @current-change="pageChange"
                       layout="total,sizes,prev,pager,next,jumper"
                       :page-size="page_size"
                       :page-sizes="[10, 30, 50]"
                       :current-page="pageNum"
                       :total="total">
        </el-pagination>
      </div>
    </el-card>

    <el-dialog title="分销员审核"
               :visible.sync="dialogFormVisible"
               width="40%">
      <span style="display:flex;margin:0 45px 20px;font-weight:600">是否通过该分销员的申请？如果同意请选择所属分组</span>
      <el-form size="small"
               label-width="140px">
        <el-form-item label="按分组查看：">
          <el-select v-model="form.groupId"
                     placeholder="默认分组">
            <el-option v-for="item in groupList"
                       :key="item.id"
                       :label="item.name"
                       :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer"
           class="dialog-footer"
           v-loading="loadingDia">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary"
                   @click="audit(1)">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="驳回申请"
               :visible.sync="dialogFormVisibleOne">
      <el-form label-width="80px"
               :model="form">
        <el-form-item label="驳回原因">
          <el-input v-model.trim="form.reason"
                    show-word-limit
                    maxlength="20"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer"
           class="dialog-footer"
           v-loading="loadingDia">
        <el-button @click="dialogFormVisibleOne = false">取 消</el-button>
        <el-button type="primary"
                   @click="audit(2)">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="查看驳回原因"
               :visible.sync="dialogFormVisibleReason"
               width="30%">
      <span style="margin-left:20px">{{ auditFalseReason }}</span>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogFormVisibleReason = false">关闭</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
import {
  findChannelUserApplyRecordList,
  groupCapacity,//查询分组人数是否超过500
  initChannelGroupList,//  分组select下拉框
  checkApplyState,  // 分销员审核操作
} from '@/api/distributionManagement/distributor'
// import testJson from './test'
export default {
  props: ["status", "shop", "user"],
  data() {
    return {
      list: {
        channelUsernickName: '',
        channelUserPhone: '',
        registerTime: '',
        status: '',
      },
      statusList: [
        {
          value: '',
          label: '全部'
        },
        {
          value: '0',
          label: '待审核'
        },
        {
          value: '2',
          label: '已驳回'
        },
        {
          value: '1',
          label: '已通过'
        },
      ],
      groupList: [],
      form: {
        reason: '',
        groupId: '',
      },
      userId: '',
      orderNo: '',
      dialogFormVisibleTwo: false,
      dialogFormVisible: false,
      dialogFormVisibleOne: false,
      dialogFormVisibleReason: false,
      auditFalseReason: '',
      id: '',
      tableList: [],
      pageNum: 1,
      page_size: 10,
      total: 0,
      loading: false,
      loadingDia: false,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime() - 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
    }
  },
  created() {
    // this.searchList()
    // this.searchGroupList()
  },
  mounted() {

  },
  methods: {
        //查询分销员分组
    searchGroupList() {
      initChannelGroupList({
        shopId: this.shop.shopId
      }).then(res => {
        if (res.code == 200) {
          this.groupList = res.data
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    //查询数据列表
    searchList() {
      this.loading = true
      findChannelUserApplyRecordList({
        shopId: this.shop.shopId,
        pageNum: this.pageNum,
        pageSize: this.page_size,
        channelUsernickName: this.list.channelUsernickName,
        channelUserPhone: this.list.channelUserPhone,
        applyState: this.list.status,
        startCreateTime: this.list.registerTime == null ? '' : this.list.registerTime[0],
        endCreateTime: this.list.registerTime == null ? '' : this.list.registerTime[1],
      }).then(res => {
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
          this.loading = false
        } else {
          this.$notify.error({
            title: '失败',
            message: res.message,
          });
        }
      })
    },

    //查看驳回原因
    showReason(row) {
      this.dialogFormVisibleReason = true
      this.auditFalseReason = row.applyComment
    },
    auditTrue(index, row) {
      if (index == 1) {
        this.dialogFormVisible = true
      }
      if (index == 2) {
        this.dialogFormVisibleOne = true
      }
      this.id = row.id
      this.form.reason = ''
      this.form.groupId = ''
      let obj = this.groupList.find(item =>{
        return item.isDefault == 1
      })
      this.form.groupId = obj.id
      this.loadingDia = false
    },

    audit(index) {
      let approvalState = ''
      if (index == 1) {
        approvalState = '1' //通过操作
        //验证人数是否超出
        groupCapacity({
          shopId: this.shop.shopId,
          groupId: this.form.groupId,
          count: 1
        }).then(res => {
          if (res.data == true) {
            this.loadingDia = true
            checkApplyState({
              shopId: this.shop.shopId,
              id: this.id,
              applyState: 1,
              channelGroupId: this.form.groupId,
            }).then(res => {
              if (res.data == true) {
                this.$notify({
                  title: '成功',
                  type: 'success',
                  message: '审核成功'
                })
                this.loadingDia = false
                this.dialogFormVisible = false
                this.searchList()
              } else {
                this.$notify.error({
                  title: "失败",
                  message: res.message
                })
              }
            })
          } else {
            this.$notify.error({
              title: "失败",
              message: res.message
            })
          }
        })
      }
      if (index == '2' && this.form.reason == '') {
        this.$notify.error({
          title: '失败',
          message: '驳回原因不能为空'
        });
        return
      }
      if (index == 2) {
        this.loadingDia = true
        checkApplyState({
          shopId: this.shop.shopId,
          id: this.id,
          applyState: 2,
          applyComment: this.form.reason,
        }).then(res => {
          if (res.data == true) {
            this.$notify({
              title: '成功',
              type: 'success',
              message: '驳回操作成功'
            })
            this.loadingDia = false
            this.dialogFormVisibleOne = false
            this.searchList()
          } else {
            this.$notify.error({
              title: "失败",
              message: res.message
            })
          }
        })
      }
    },

    //重置
    reset() {
      this.list = {
        channelUsernickName: '',
        channelUserPhone: '',
        registerTime: '',
        status: '',
      }
      this.searchList()
    },
    //修改每页页数
    handleSizeChange(page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange(page) {
      this.pageNum = page
      this.searchList()
    },
  },
  computed: {
    startTime() {
      return this.status;
    },
  },
  watch: {
    startTime(newValue, oldValue) {
      this.searchList()
      this.searchGroupList()
      return newValue;
    },
  }
}
</script>

<style lang="scss" scoped>
.button_mian {
  margin: 5px;
}
.operate-container {
  margin-top: 10px;
}
.app-container {
  // padding: 20px 20px 40px 20px;
}
.btn-add {
  float: right;
  margin-top: -8px;
}
.store-red {
  margin-left: 70px;
  color: red;
  span {
    display: block;
    margin-bottom: 10px;
  }
}
.table-container {
  margin-top: 10px;
}
.fixedB {
  width: 100%;
  padding-right: 300px;
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  background: #eee;
  z-index: 100;
  margin-left: -20px;
  .el-pagination {
    display: flex;
    align-items: center;
  }
}
</style>
